  -webkit-box-shadow: 0 1px 0 rgba(9, 30, 66, 0.25);
          box-shadow: 0 1px 0 rgba(9, 30, 66, 0.25);
  line-height: 20px;
  width: 100%;
  height: 66px;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
