<script>
    // props
    import {tick, createEventDispatcher, onDestroy} from "svelte";
    import {autoFocusout} from "~/actions/autoFocusout";
    import { cards } from "~/store/list";

    export let listId
    export let card

    let isEditMode = false
    let title
    let textareaEl
    const dispatch = createEventDispatcher()

    async function onEditMode(){
        isEditMode = true
        title = card.title
        dispatch('editMode', true)
        await tick()
        textareaEl && textareaEl.focus()
    }
    function offEditMode(){
        isEditMode = false
        dispatch('editMode', false)
    }
    function saveCard(){
        if(title.trim()){
            cards.edit({
                listId,
                cardId: card.id,
                title,
            })
        }
        offEditMode()
    }
    function removeCard(){
        cards.remove({
            listId,
            cardId: card.id
        })
        offEditMode()
    }

    onDestroy( () => {
        offEditMode()
    })
</script>
<div class="card">
    {#if isEditMode}
        <div
                use:autoFocusout={offEditMode}
                class="edit-mode">
            <textarea
                    bind:value={title}
                    bind:this={textareaEl}
                    placeholder="Enter a title for this card..."
                    on:keydown={ event => {
                        event.key === 'Enter' && saveCard()
                        event.key === 'Escape' && offEditMode()
                        event.key === 'Esc' && offEditMode()
                    }}
            ></textarea>
            <div class="actions">
                <div
                        class="btn success"
                        on:click={saveCard}
                >Save</div>
                <div
                        class="btn"
                        on:click={offEditMode}
                >Cancel</div>
                <div
                        class="btn danger"
                        on:click={removeCard}
                >Delete Card</div>
            </div>
        </div>
    {:else}
        <div class="title">
            {card.title}
            <div
                    class="btn small"
                    on:click={ onEditMode }
            >
                Edit
            </div>
        </div>
    {/if}
</div>

<style lang="scss">.actions {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  padding-bottom: 10px; }
  .actions .btn {
    margin-right: 4px; }

.btn {
  display: inline-block;
  padding: 6px 12px;
  background-color: #e2e6ea;
  border-radius: 4px;
  line-height: 20px;
  cursor: pointer;
  font-size: 14px;
  font-weight: 400; }
  .btn:hover {
    background-color: #c4cdd5; }
  .btn.success {
    background-color: #61bd4f;
    color: #fff; }
    .btn.success:hover {
      background-color: #4b9e3b; }
  .btn.danger {
    background-color: #eb5a46;
    color: #fff; }
    .btn.danger:hover {
      background-color: #e53219; }
    .btn.danger::after {
      content: '!'; }
  .btn.small {
    font-size: 12px;
    padding: 0 6px; }

.edit-mode textarea {
  resize: none;
  outline: none;
  border: none;
  background: #FFF;
  margin-bottom: 8px;
  border-radius: 4px;
  -webkit-box-shadow: 0 1px 0 rgba(9, 30, 66, 0.25);
          box-shadow: 0 1px 0 rgba(9, 30, 66, 0.25);
  line-height: 20px;
  width: 100%;
  height: 66px;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  display: block; }

.card {
  margin-bottom: 8px; }
  .card:last-child {
    margin-bottom: 1px; }
  :global(.card.sortable-ghost) {
    position: relative;
    opacity: 0.1; }
    :global(.card.sortable-ghost)::after {
      content: '';
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      background: #000;
      border-radius: 4px; }
  :global(.card.sortable-chosen) {
    cursor: move; }
  .card .title {
    background-color: #fff;
    padding: 6px 8px;
    border-radius: 4px;
    line-height: 20px;
    -webkit-box-shadow: 0 1px 0 rgba(9, 30, 66, 0.25);
            box-shadow: 0 1px 0 rgba(9, 30, 66, 0.25);
    position: relative; }
    .card .title:hover {
      background-color: #f5f5f5; }
    .card .title .btn {
      position: absolute;
      right: 10px;
      top: 6px;
      display: none; }
    .card .title:hover .btn {
      display: block; }</style>