    .btn.success:hover {
      background-color: #4b9e3b; }
  .btn.danger {
    background-color: #eb5a46;
    color: #fff; }
    .btn.danger:hover {
      background-color: #e53219; }
    .btn.danger::after {
      content: '!'; }
  .btn.small {
    font-size: 12px;
    padding: 0 6px; }

.edit-mode textarea {
  resize: none;
  outline: none;
  border: none;
  background: #FFF;
  margin-bottom: 8px;
  border-radius: 4px;
  -webkit-box-shadow: 0 1px 0 rgba(9, 30, 66, 0.25);
          box-shadow: 0 1px 0 rgba(9, 30, 66, 0.25);
  line-height: 20px;
  width: 100%;
  height: 66px;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  display: block; }

.create-list {
  width: 290px;
  display: inline-block;
  white-space: normal;
  font-size: 1rem;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  padding: 10px 8px;
  vertical-align: top;
  /* SCSS에서 제공해주는 rgba함수 */
  background: rgba(235, 236, 240, 0.6);
  border-radius: 4px;
  margin: 0 4px;
  line-height: 20px;
  cursor: pointer;
  -webkit-transition: 0.2s;
  transition: 0.2s;
  /* & 중첩되어 있는 해당영역의 선택자를 의미한다. */ }
  .create-list:hover {
    background: #ebecf0; }</style>