<script>
    import { autoFocusout } from "~/actions/autoFocusout";
    import { tick, createEventDispatcher, onDestroy } from "svelte";
    import { lists } from "~/store/list";

    export let list

    let isEditMode = false
    let title = list.title
    let textareaEl
    const dispatch = createEventDispatcher()

    function  saveTitle(){
        if(title.trim()){
            lists.edit({
                listId: list.id,
                title
            })

        }
        offEditMode()
    }
    function removeList(){
        lists.remove({
            listId: list.id
        })
        offEditMode()
    }
    async function onEditMode(){
        isEditMode = true
        title = list.title
        dispatch('editMode', true)
        await tick()
        textareaEl && textareaEl.focus()
    }
    function offEditMode(){
        isEditMode = false
        dispatch('editMode', false)
    }

    // 삭제 후 offEditMode를 호출해도 되지만
    // 해당 컴포넌트가 삭제될 때 호출해도 된다.
    onDestroy( () => {
        offEditMode()
    })


</script>

{#if isEditMode}
    <div
            use:autoFocusout={offEditMode}
            class="edit-mode"
    >
        <textarea
                bind:value={title}
                bind:this={textareaEl}
                on:keydown={ event => {
                    event.key === 'Enter' && saveTitle()
                    event.key === 'Escape' && offEditMode()
                    event.key === 'Esc' && offEditMode()
                }}
        ></textarea>
        <div class="actions">
            <div
                    class="btn success"
                    on:click={saveTitle}
            >Save</div>
            <div
                    class="btn"
                    on:click={offEditMode}
            >Cancel</div>
            <div
                    class="btn danger"
                    on:click={removeList}
            >Delete List</div>
        </div>
    </div>


{:else}
    <h2 class="title">
        {list.title}
        <div
                class="btn small"
                on:click={onEditMode}
        >Edit</div>
    </h2>
{/if}

<style lang="scss">@charset "UTF-8";
.actions {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  padding-bottom: 10px; }
  .actions .btn {
    margin-right: 4px; }

.btn {
  display: inline-block;
  padding: 6px 12px;
  background-color: #e2e6ea;
  border-radius: 4px;
  line-height: 20px;
  cursor: pointer;
  font-size: 14px;
  font-weight: 400; }
  .btn:hover {
    background-color: #c4cdd5; }
  .btn.success {
    background-color: #61bd4f;
    color: #fff; }
    .btn.success:hover {
      background-color: #4b9e3b; }
  .btn.danger {
    background-color: #eb5a46;
    color: #fff; }
    .btn.danger:hover {
      background-color: #e53219; }
    .btn.danger::after {
      content: '!'; }
  .btn.small {
    font-size: 12px;
    padding: 0 6px; }

.edit-mode textarea {
  resize: none;
  outline: none;
  border: none;
  background: #FFF;
  margin-bottom: 8px;
  border-radius: 4px;
  -webkit-box-shadow: 0 1px 0 rgba(9, 30, 66, 0.25);
          box-shadow: 0 1px 0 rgba(9, 30, 66, 0.25);
  line-height: 20px;
  width: 100%;
  height: 66px;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  display: block; }

h2.title {
  position: relative;
  font-weight: 700;
  padding: 4px 8px;
  cursor: pointer; }
  h2.title .btn {
    position: absolute;
    top: 0;
    right: 0;
    display: none; }

/* ListTitle 컴포넌트에는 list__inner가 없다.
       global 수식어로 묶어준다.
     */
:global(.list__inner:hover .list__heading h2 .btn) {
  display: block; }</style>
